import axiosInstance from "@/api/axios/axiosInstance";

export interface ClinicalRole {
  id: number;
  name: string;
  display_name: string;
  description: string;
  permissions: {
    view: string[];
    add: string[];
    update: string[];
    delete: string[];
  };
  hierarchy_level: number;
  is_system_role: boolean;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

export interface ClinicalRoleStats {
  total_roles: number;
  system_roles: number;
  custom_roles: number;
  users_with_roles: number;
}

export interface CreateClinicalRoleData {
  name: string;
  display_name: string;
  description?: string;
  permissions: {
    view: string[];
    add: string[];
    update: string[];
    delete: string[];
  };
  hierarchy_level?: number;
}

export interface UpdateClinicalRoleData extends CreateClinicalRoleData {
  id: number;
}

export interface UserWithRole {
  id: number;
  username: string;
  email: string;
  first_name: string;
  last_name: string;
  assigned_at: string;
  expires_at?: string;
  is_active: boolean;
}

// Get all clinical roles
export const getAllClinicalRoles = async () => {
  try {
    const response = await axiosInstance.get("/api/clinical-roles");
    return response.data;
  } catch (error) {
    console.error("Error fetching clinical roles:", error);
    throw error;
  }
};

// Get clinical role by ID
export const getClinicalRoleById = async (id: number): Promise<ClinicalRole | null> => {
  try {
    const response = await axiosInstance.get(`/api/clinical-roles/${id}`);
    return response.data.data;
  } catch (error) {
    console.error("Error fetching clinical role:", error);
    return null;
  }
};

// Create new clinical role
export const createClinicalRole = async (roleData: CreateClinicalRoleData): Promise<ClinicalRole | null> => {
  try {
    const response = await axiosInstance.post("/api/clinical-roles", roleData);
    return response.data.data;
  } catch (error) {
    console.error("Error creating clinical role:", error);
    throw error;
  }
};

// Update clinical role
export const updateClinicalRole = async (id: number, roleData: Partial<CreateClinicalRoleData>): Promise<ClinicalRole | null> => {
  try {
    const response = await axiosInstance.put(`/api/clinical-roles/${id}`, roleData);
    return response.data.data;
  } catch (error) {
    console.error("Error updating clinical role:", error);
    throw error;
  }
};

// Delete clinical role
export const deleteClinicalRole = async (id: number): Promise<boolean> => {
  try {
    await axiosInstance.delete(`/api/clinical-roles/${id}`);
    return true;
  } catch (error) {
    console.error("Error deleting clinical role:", error);
    throw error;
  }
};

// Get clinical role statistics
export const getClinicalRoleStats = async (): Promise<ClinicalRoleStats> => {
  try {
    const response = await axiosInstance.get("/api/clinical-roles/stats");
    return response.data.data;
  } catch (error) {
    console.error("Error fetching clinical role stats:", error);
    throw error;
  }
};

// Get users with specific clinical role
export const getUsersWithRole = async (roleId: number): Promise<{ role: ClinicalRole; users: UserWithRole[] }> => {
  try {
    const response = await axiosInstance.get(`/api/clinical-roles/${roleId}/users`);
    return response.data.data;
  } catch (error) {
    console.error("Error fetching users with role:", error);
    throw error;
  }
};

// Assign clinical role to user
export const assignRoleToUser = async (roleId: number, userId: number, expiresAt?: string): Promise<boolean> => {
  try {
    await axiosInstance.post(`/api/clinical-roles/${roleId}/users/${userId}`, {
      expires_at: expiresAt
    });
    return true;
  } catch (error) {
    console.error("Error assigning role to user:", error);
    throw error;
  }
};

// Remove clinical role from user
export const removeRoleFromUser = async (roleId: number, userId: number): Promise<boolean> => {
  try {
    await axiosInstance.delete(`/api/clinical-roles/${roleId}/users/${userId}`);
    return true;
  } catch (error) {
    console.error("Error removing role from user:", error);
    throw error;
  }
};
